import makeNewState from "./makeNewState"

export default function makeOnChanged(state, settings, setters, dispatch, action) {
  return (name, event) => {
    const value = event.target.value
    setters[name](value)
    try {
      dispatch(
        action(makeNewState(
          state,
          settings,
          {
            [name]: value || settings[name]
          }
        ))
      )
    } catch (err) {}
  }
}
