import { createSlice } from '@reduxjs/toolkit'
import { initialState } from './initialState'

const doUnitMath = (state, stateUpdate) => {
  Object.assign(state, stateUpdate)

  const carbsPerColumnWeight = state.carbsPerColumnWeight
  const columnWeight = state.columnWeight
  const totalPacketWeight = state.totalPacketWeight
  const totalPieces = state.totalPieces
  const consumedPieces = state.consumedPieces
  const minimumCarbs = state.minimumCarbs

  const carbRatio = columnWeight ? (carbsPerColumnWeight / columnWeight) : 0
  const weightRatio = columnWeight ? (totalPacketWeight / columnWeight) : 0
  const consumedRatio = totalPieces ? (consumedPieces / totalPieces) : 0
  const consumedCarbs = (carbRatio * weightRatio * consumedRatio * totalPacketWeight);

  const roundedConsumedCarbs = Math.round(consumedCarbs / minimumCarbs) * minimumCarbs

  Object.assign(state, {
    carbsPerColumnWeight,
    columnWeight,
    totalPacketWeight,
    totalPieces,
    consumedPieces,
    minimumCarbs,
    carbRatio,
    weightRatio,
    consumedRatio,
    consumedCarbs,
    roundedConsumedCarbs
  })

}

const proportionateCarbsSlice = createSlice({
  name: 'proportionateCarbs',
  initialState: initialState.proportionateCarbs,
  reducers: {
    allProportionateCarbsChanged(state, action) {
      doUnitMath(state, action.payload)
    }
  }
})

export const {
  allProportionateCarbsChanged
} = proportionateCarbsSlice.actions

export default proportionateCarbsSlice.reducer
