import doStringMath from './doStringMath';

export default function makeNewState(state, settings, update = {}) {
  const merged = {}
  for (const key in update) {
    update[key] = doStringMath(update[key])
  }
  for (const key in state) {
    merged[key] = update[key] ?? state[key] ?? settings[key]
  }
  return merged
}
